import React, { Component } from 'react'
import GridUnit from '../components/GridUnit'
import Section from '../components/Section'
import Button from '../components/Button'
import ScrollableAnchor from 'react-scrollable-anchor'
import './style.scss'
import '../components/Button/style.scss'

export default class IndexPage extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    const { edges: posts } = this.props.data.allMarkdownRemark

    return (
      <div className="index">
        <div className="main">
          {/* Header */}
          <div className="row">
            <div className="col-xs-12 col-sm-8 col-md-9">
              <h5>
                Hi, I'm <span className="bold">Or Fleisher</span>
              </h5>

              <h3 className="bold">
                Creative technologist, developer and artist working at the
                intersection of technology and storytelling. Combining computer
                graphics, machine learning and immersive experience development
                in virtual, augmented and mixed realities.
              </h3>
            </div>
          </div>

          <div className="row">
            <Button
              text="Biography"
              link="/bio"
              mobileWidth={'6'}
              smallWidth={'4'}
              mediumWidth={'3'}
            />
            <a
              href="#contact"
              style={{ textDecoration: 'none' }}
              className="col-xs-6 col-sm-4 col-md-3"
            >
              <div className="btn">Contact</div>
            </a>
          </div>
          {/* Projects */}
          <Section title="Projects">
            <div className="row">
              {posts.map(({ node: post }) => {
                const { frontmatter } = post

                //Bio page doesn't get a grid cube
                if (post.frontmatter.title == 'Bio') return

                let tags = ''
                for (let tag in frontmatter.tags) {
                  if (tag != frontmatter.tags.length - 1) {
                    tags += frontmatter.tags[tag] + ' / '
                  } else {
                    tags += frontmatter.tags[tag]
                  }
                }
                return (
                  <GridUnit
                    key={post.frontmatter.path}
                    alt_text={post.frontmatter.path}
                    logo={require('../assets/images/gifs' +
                      frontmatter.path +
                      '.gif')}
                    colour="#BDBDBD"
                    title={frontmatter.title}
                    link={frontmatter.path}
                    subtag={tags}
                    subtitle={frontmatter.excerpt}
                  />
                )
              })}
            </div>
          </Section>
          <ScrollableAnchor id={'contact'}>
            <Section title="">
              <p>
                Email me at <span className="bold">contact@orfleisher.com</span>
              </p>
              <div className="row">
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://github.com/juniorxsound"
                >
                  <img
                    alt="Github"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://twitter.com/juniorxsound"
                >
                  <img
                    alt="Twitter"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://facebook.com/or.fleisher"
                >
                  <img
                    alt="Facebook"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://linkedin.com/in/orfleisher/"
                >
                  <img
                    alt="Linkedin"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="https://www.youtube.com/channel/UCLCfTDhutnzsBoxeSlUls9A"
                >
                  <img
                    alt="YouTube"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://vimeo.com/orfleisher"
                >
                  <img
                    alt="Vimeo"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
                <a
                  rel="noopener"
                  target="_blank"
                  href="http://instagram.com/juniorxsound/"
                >
                  <img
                    alt="Instagram"
                    src="data:image/svg+xml;base64,"
                  />
                </a>
                &nbsp;&nbsp;
              </div>
            </Section>
          </ScrollableAnchor>
        </div>
      </div>
    )
  }
}

export const pageQuery = graphql`
  query pageAndImageQuery {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            thumbnail
            date(formatString: "MMMM DD, YYYY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`



